"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Textarea } from "@/components/ui/textarea"
import { Bell, Menu, Plus, Search } from "lucide-react"
import { useAuthStore } from "@/lib/auth-store"
import { ManagerSidebar } from "@/components/manager/manager-sidebar"
import { DashboardStats } from "@/components/manager/dashboard-stats"
import { AppointmentsList } from "@/components/manager/appointments-list"
import { PopularServices } from "@/components/manager/popular-services"
import { StaffPlanning } from "@/components/manager/staff-planning"
import { AnalyticsTab } from "@/components/manager/analytics-tab"
import { ClientsTab } from "@/components/manager/clients-tab"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { EmployeePerformance } from "@/components/manager/employee-perfomance"
import { ServicesTab } from "@/components/manager/service-tab"
import { StaffTab } from "@/components/manager/staff-tab"
import { useToast } from "@/components/ui/use-toast"



export default function ManagerDashboard() {
  const router = useRouter()
  const { user, isAuthenticated } = useAuthStore()
  const { toast } = useToast()
  const [sidebarOpen, setSidebarOpen] = useState(false)
  const [activeTab, setActiveTab] = useState("dashboard")

  // Clients & RDV
 type Client = {
  _id: string
  prenom: string
  nom: string
  email?: string
  telephone: string
  notes?: string
}

const [clients, setClients] = useState<Client[]>([])

// Filter clients
const filteredClients = clients.filter((c: Client) =>
  `${c.prenom} ${c.nom}`.toLowerCase().includes(searchTerm.toLowerCase()) ||
  (c.email && c.email.toLowerCase().includes(searchTerm.toLowerCase())) ||
  c.telephone.includes(searchTerm)
)

  const [searchTerm, setSearchTerm] = useState("")

  const [clientForm, setClientForm] = useState({
    prenom: "",
    nom: "",
    telephone: "",
    email: "",
    notes: "",
  })

  const [form, setForm] = useState({
    clientId: "",
    date: "",
    time: "",
    service: "",
    coiffeur: "",
    notes: "",
  })

  const [openClientModal, setOpenClientModal] = useState(false)
  const [openRdvModal, setOpenRdvModal] = useState(false)

const handleClientChange = (key: keyof typeof clientForm, value: string) => {
  setClientForm(prev => ({ ...prev, [key]: value }))
}


  const handleRdvChange = (key: keyof typeof form, value: string) => {
  setForm(prev => ({ ...prev, [key]: value }))
}


  // Auth redirect
  useEffect(() => {
    if (!isAuthenticated || (user?.role !== "gerant" && user?.role !== "admin")) {
      router.push("/auth/login")
    }
  }, [isAuthenticated, user, router])

  // Filter clients
  const filteredClients = clients.filter(
    c =>
      `${c.prenom} ${c.nom}`.toLowerCase().includes(searchTerm.toLowerCase()) ||
      (c.email && c.email.toLowerCase().includes(searchTerm.toLowerCase())) ||
      c.telephone.includes(searchTerm)
  )

  // Create client
  const handleCreateClient = async () => {
    try {
      const res = await fetch("http://localhost:3500/api/createclient", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(clientForm),
      })
      const data = await res.json()
      if (!res.ok) throw new Error(data.message || "Erreur lors de l'ajout")

      toast({ title: "Client enregistré !" })
      setClientForm({ prenom: "", nom: "", telephone: "", email: "", notes: "" })
      setOpenClientModal(false)
      setClients(prev => [...prev, data])
    } catch (err:any) {
      toast({ title: "Erreur", description: err.message, variant: "destructive" })
    }
  }

  // Create RDV
  const handleCreateRdv = async () => {
    if (!form.clientId || !form.date || !form.time || !form.service || !form.coiffeur) {
      toast({ title: "Erreur", description: "Veuillez remplir tous les champs obligatoires", variant: "destructive" })
      return
    }

    try {
      const res = await fetch("http://localhost:3500/api/createrdv", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      })
      const data = await res.json()
      if (!res.ok) throw new Error(data.message || "Erreur lors de la création")
      toast({ title: "Rendez-vous ajouté !" })
      setForm({ clientId: "", date: "", time: "", service: "", coiffeur: "", notes: "" })
      setOpenRdvModal(false)
    } catch (err:ant) {
      toast({ title: "Erreur", description: err.message, variant: "destructive" })
    }
  }

  if (!isAuthenticated || (user?.role !== "gerant" && user?.role !== "admin")) return null

  return (
    <div className="flex h-screen bg-gray-50 dark:bg-gray-950">
      <ManagerSidebar
        isOpen={sidebarOpen}
        onClose={() => setSidebarOpen(false)}
        activeTab={activeTab}
        onTabChange={setActiveTab}
      />

      <div className="flex-1 flex flex-col overflow-hidden">
        {/* Header */}
        <header className="bg-white dark:bg-gray-900 border-b min-h-16 md:h-20 flex items-center justify-between px-4 md:px-6 gap-2">
          <div className="flex items-center gap-2 md:gap-4 min-w-0">
            <Button variant="ghost" size="icon" className="lg:hidden shrink-0" onClick={() => setSidebarOpen(true)}>
              <Menu className="w-5 h-5" />
            </Button>
            <h1 className="text-lg md:text-2xl font-bold truncate">Tableau de bord</h1>
          </div>
          <div className="flex items-center gap-2 md:gap-3 shrink-0">
            <Button variant="outline" className="hidden sm:flex bg-cyan-500 hover:bg-cyan-600 text-white border-cyan-500" onClick={() => setOpenClientModal(true)}>
              <Plus className="w-4 h-4 mr-2" /> Enregistrer un client
            </Button>
            <Button className="bg-cyan-500 hover:bg-cyan-600" onClick={() => setOpenRdvModal(true)}>
              <Plus className="w-4 h-4 mr-2" /> Ajouter un rendez-vous
            </Button>
            <Button variant="ghost" size="icon" className="shrink-0"><Bell className="w-5 h-5" /></Button>
          </div>
        </header>

        {/* MODAL CLIENT */}
        <Dialog open={openClientModal} onOpenChange={setOpenClientModal}>
          <DialogContent className="sm:max-w-xl">
            <DialogHeader><DialogTitle>Nouveau client</DialogTitle></DialogHeader>
            <div className="grid gap-4 py-4">
              <Input placeholder="Prénom" value={clientForm.prenom} onChange={e => handleClientChange("prenom", e.target.value)} />
              <Input placeholder="Nom" value={clientForm.nom} onChange={e => handleClientChange("nom", e.target.value)} />
              <Input placeholder="Téléphone" value={clientForm.telephone} onChange={e => handleClientChange("telephone", e.target.value)} />
              <Input type="email" placeholder="Email" value={clientForm.email} onChange={e => handleClientChange("email", e.target.value)} />
              <Textarea placeholder="Notes" value={clientForm.notes} onChange={e => handleClientChange("notes", e.target.value)} />
            </div>
            <DialogFooter>
              <Button variant="outline" onClick={() => setOpenClientModal(false)}>Annuler</Button>
              <Button onClick={handleCreateClient}>Enregistrer</Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>

        {/* MODAL RDV */}
        <Dialog open={openRdvModal} onOpenChange={setOpenRdvModal}>
          <DialogContent className="sm:max-w-2xl max-h-[90vh] overflow-y-auto">
            <DialogHeader><DialogTitle>Ajouter un rendez-vous</DialogTitle></DialogHeader>
            <div className="grid gap-4 py-4">
              <div>
                <Label>Client</Label>
                <Input placeholder="Rechercher..." value={searchTerm} onChange={e => setSearchTerm(e.target.value)} className="pl-10"/>
                <Select onValueChange={val => handleRdvChange("clientId", val)} className="mt-2">
                  <SelectTrigger><SelectValue placeholder="Sélectionner un client" /></SelectTrigger>
                  <SelectContent>
                    {filteredClients.map(c => <SelectItem key={c._id} value={c._id}>{c.prenom} {c.nom} ({c.telephone})</SelectItem>)}
                  </SelectContent>
                </Select>
              </div>
              <Input type="date" value={rdvForm.date} onChange={e => handleRdvChange("date", e.target.value)} />
              <Input type="time" value={rdvForm.time} onChange={e => handleRdvChange("time", e.target.value)} />
              <Select onValueChange={val => handleRdvChange("service", val)}>
                <SelectTrigger><SelectValue placeholder="Service" /></SelectTrigger>
                <SelectContent>
                  <SelectItem value="coupe">Coupe</SelectItem>
                  <SelectItem value="couleur">Couleur</SelectItem>
                  <SelectItem value="tresses">Tresses Africaines</SelectItem>
                  <SelectItem value="soin">Soin</SelectItem>
                </SelectContent>
              </Select>
              <Select onValueChange={val => handleRdvChange("coiffeur", val)}>
                <SelectTrigger><SelectValue placeholder="Coiffeur" /></SelectTrigger>
                <SelectContent>
                  <SelectItem value="amina">Amina</SelectItem>
                  <SelectItem value="fatou">Fatou</SelectItem>
                  <SelectItem value="yasmine">Yasmine</SelectItem>
                </SelectContent>
              </Select>
              <Textarea placeholder="Notes" value={rdvForm.notes} onChange={e => handleRdvChange("notes", e.target.value)} />
            </div>
            <DialogFooter>
              <Button variant="outline" onClick={() => setOpenRdvModal(false)}>Annuler</Button>
              <Button onClick={handleCreateRdv}>Créer le rendez-vous</Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>
      </div>
    </div>
  )
}
